import * as React from 'react';
import * as ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { createBrowserHistory } from 'history';
import { ConnectedRouter } from 'connected-react-router';
import Index from './pages';
import configureStore from './store/configureStore';
import * as serviceWorker from './serviceWorker';
import { UserContext } from './user/userContext';
import { getAndUpdateLocalUser } from './user/userUtils';

declare global {
    interface Window { initialReduxState: any; }
}

window.initialReduxState = window.initialReduxState || {};

const initialState = window.initialReduxState;
const history = createBrowserHistory();

const store = configureStore(history, initialState);

const localUser = getAndUpdateLocalUser();

const Root = () => (
    <Provider store={store}>
        <UserContext.Provider value={localUser}>
            <ConnectedRouter history={history}>
                <Index />
            </ConnectedRouter>
        </UserContext.Provider>
    </Provider>
);

ReactDOM.render(<Root />, document.querySelector('#root'));

serviceWorker.unregister();
