import { User } from './user';

export const cookieUserKey = 'user';
export const storedUserKey = 'user';

const getCookie = (name: string): string | undefined => {
    const value = '; ' + document.cookie;
    const parts: string[] = value.split('; ' + name + '=');

    if (parts.length === 2) {
        // return parts.pop().split(';').shift();
        const part = parts.pop();
        if (part) {
            return part.split(';').shift();
        }
    }

    return undefined;
};

const deleteCookie = (name: string) => {
    const date = new Date();

    // Set it expire in -1 days
    date.setTime(date.getTime() + (-1 * 24 * 60 * 60 * 1000));

    // Set it
    document.cookie = name + '=; expires=' + date.toUTCString() + '; path=/';
};

const getCookieUser = (): User | null => {
    const cookie = getCookie(cookieUserKey);
    return cookie ? JSON.parse(cookie) : null;
};

const getStoredUser = (): User | null => {
    const item = localStorage.getItem(storedUserKey);
    return item ? JSON.parse(item) : null;
};

const equals = (user1: User, user2: User): boolean => {
    return user1.id === user2.id && user1.name === user2.name && user1.avatar === user2.avatar;
};

export const getAndUpdateLocalUser = (): User | null => {
    const cookieUser = getCookieUser();
    const storedUser = getStoredUser();

    if (cookieUser || storedUser) {
        if (cookieUser && storedUser) {
            if (!equals(cookieUser, storedUser)) {
                localStorage.setItem(storedUserKey, JSON.stringify(cookieUser));
            }
            return cookieUser;
        }

        if (cookieUser) {
            localStorage.setItem(storedUserKey, JSON.stringify(cookieUser));
            return cookieUser;
        }

        if (storedUser) {
            return storedUser;
        }
    }

    return null;
};

export const signOutInLocal = (): void => {
    deleteCookie(cookieUserKey);
    localStorage.removeItem(storedUserKey);
};
